*{

  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.logo {
font-size: 1.52rem;
color: #fff;
margin-left: 20px;
}
.navbar{
  background-color: #F4BE2C;
  width: 100vw;

  font-family: 'Pacifico', cursive;
}

.box{
display: flex;
justify-content: center;
align-items: center;
padding-top: 20px;
}

.main{
  width: 430px;
  height: 150px;
  margin: 10px;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  
  border: 2px solid white;
box-shadow: 2px 2px 2px 2px #CAD5E2;
cursor: pointer;
border-radius: 10px;
  
}

input{
  width: 100%;
  border: none;
  margin: 10px;
outline: none;
height: 40px;
/* border-radius: %; */
background: transparent;
/* border: 2px solid white; */
/* box-shadow: 2px 2px 2px 2px #CAD5E2; */
cursor: pointer;


border-radius: 10px;
}

input::placeholder
{
  /* position: relative;
  left: 10px; */
color: black;
}
body{
  background: white;

}


input:focus { 
  outline: none !important;
}
textarea{


  position: relative;
left: 10px;
width: 100%;
border: none;
margin-top: 10px;
outline: none;
height: 40px;
background: transparent;
cursor: pointer;
}

#addbutton{

position: relative;
left: 95%;
border: none;
color: #F4BE2C;
box-shadow: 1px 1px 1px 1px white;


}

.output{

  width: 250px;
  height: 150px;
  margin: 10px;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  position: relative;
  top: 150px;
  left: 20px;
  border: 2px solid white;
box-shadow: 2px 2px 2px 2px #CAD5E2;
cursor: pointer;
border-radius: 10px;

}

.output p{

  position: relative;
  bottom: -60px;
}


.outbtn{
  position: relative;
  bottom: -110px;
color: #F4BE2C;
box-shadow: 1px 1px 1px 1px white;
  left: 40px;
}


.item-list{


  display: flex;
  flex-wrap: wrap;
margin: 10px;
justify-content: center;
}

@media screen and (max-width: 420px) {
 
  .navbar{
    background-color: red;
    width: 100vw;
  
    font-family: 'Pacifico', cursive;
  }

  .main{
    width: 330px;
    height: 150px;
  
  }

  .box{
    display: flex;
    
    }

    #addbutton{

      position: relative;
      left: 90%;
      
      
      }
      
.outbtn{
  position: relative;
  bottom: -110px;
  left: 0 ;
}
    
.output{

  
  width: 300px;
  height: 150px;
}

.item-list{

display: block;

}

}